// API
import request from '../utils/request.js'
/**
 *
 * 
 * -------------------
 * @param channel_id  ID
 * @param timestamp    
 * -------------------
*/
export const getArticles = params => request({
  url: '/v1_0/articles',
  method: 'GET',
  params
})

/**
 *
 * 
 * -------------------
 * @param article_id  ID
 * -------------------
*/
export const getArticleById = articleId => request({
  url: `/v1_0/articles/${articleId}`,
  method: 'GET'
})

/**
 *
 * 
 * -------------------
 * @param target  ID
 * -------------------
*/
export const addFollow = target => request({
  url: '/v1_0/user/followings',
  method: 'POST',
  data: {
    target: target
  }
})

/**
 *
 * 
 * -------------------
 * @param target  ID
 * -------------------
*/
export const deleteFollow = target => request({
  url: `/v1_0/user/followings/${target}`,
  method: 'DELETE'
})

