// 
import request from '../utils/request.js'

/**
 * 
 * params  type a c
 * source id id  id
 * offset   ---- null
 *
*/
export const getComments = params => request({
  url: '/v1_0/comments',
  method: 'GET',
  params
})

/**
 *
 * 
 * -------------------
 * @param target  ID
 * -------------------
*/
export const addCommentLick = target => request({
  url: '/v1_0/comment/likings',
  method: 'POST',
  data: {
    target: target
  }
})

/**
 *
 * 
 * -------------------
 * @param target  ID
 * -------------------
*/
export const deleteCommentLick = target => request({
  url: `/v1_0/comment/likings/${target}`,
  method: 'DELETE'
})

/**
 *
 *   
 * -------------------
 * @param target idid
 * content 
 * art_id  -targetid,idtarget
 * -------------------
*/
export const addComment = data => request({
  url: '/v1_0/comments',
  method: 'POST',
  data
})
